<template>
  <!--  <model='loginForm' loginForm存储的使用用户名和密码的值的对象，通过model传给el-form组件
  rules='rules' rules是date中 定义的对象目的是校验用户名和密码的规则
  ref='ruleForm'可以通过ref来获取el-form组件内部的方法
  >-->
  <div id="login">
    <img src="../../static/pexels-sindre-strøm-1175136.jpg" style="width: 1920px; height: 1280px; left: 0px; top: 0px; position: absolute; border-radius: 0px; box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 0px;">
    <el-row>
      <el-col :span="24"><div ></div></el-col>
    </el-row>
    <el-row>
      <el-col :span="24"><div ></div></el-col>
    </el-row>
    <el-row>
      <el-col :span="24"><div >
      </div></el-col>
    </el-row>
    <el-row>
      <el-col :span="24"><div ></div></el-col>
    </el-row>
    <el-row>
      <el-col :span="5" :offset="14" ><div class="grid-content bg-purple-light">
        <el-container style="height:350px;width: 400px; border: 1px solid #eee" >
          <el-header style="height: auto" >酒店订购系统</el-header>
          <el-main style="height: auto">
            <el-form  :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm" >
              <el-form-item  label="用户名" prop="uname">
                <el-input  v-model="ruleForm.uname" ></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="upassword">
                <el-input type="password" v-model="ruleForm.upassword" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                <el-button @click="resetForm('ruleForm')">注册</el-button>
              </el-form-item>
            </el-form>
          </el-main>
          <el-footer>忘记密码？</el-footer>
        </el-container>


      </div></el-col>
    </el-row>
  </div>

</template>

<script>
import axios from "axios";
import router from "@/router";
export default {
  name:'LoginBegin',
  data() {
    var checkUser = (rule, value, callback) => {
      // console.log(value)
      if (value.trim() == '') {
        callback(new Error('请输入账号'));
      }
      else{
        callback();
      }

    };

    var checkPass = (rule,value,callback) => {
      // console.log(value)
      if (value.trim() == '') {
        callback(new Error('请输入密码'));
      }
      else{
        callback();
      }
    }


    return {
      ruleForm: {
        uname:  '',
        upassword: '',
      },

      rules: {
        uname:[
          {validator:checkUser,trigger:'blur'}
        ],
        upassword:[
          {validator:checkPass,trigger:'blur'}
        ]
      }
    };
  },
  methods: {

    submitForm(formName) {

      this.$cookies.set('uname',this.ruleForm.uname);
      this.$cookies.set('upassword',this.ruleForm.upassword)
      this.$refs[formName].validate((valid) => {
        if(valid)
        {
          axios.post('/back/user/t2',"uname="+this.ruleForm.uname+"&upassword="+this.ruleForm.upassword).then(function (response){
            console.log(response)
            if(response.status === 200)
            {
              console.log(response.data.result)
              if(response.data.result =="success"){
                router.push('/main')
              }
            }
          }).catch(failResponse => {}).catch(error => {
            console.log(error.response.data.code)
          })

        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      router.push('/register')

    }
  }

}
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}

.el-form-item__label{
  color:#409EFF;
}

.el-row  :last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}

.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

.el-header, .el-footer {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #D3DCE6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;
  line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>