import Vue from 'vue'
import Router from 'vue-router'
import LoginBegin from '@/pages/LoginBegin'
// import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
import helloWorld from "@/components/HelloWorld";
import Vuex from 'vuex';
Vue.use(Router)
Vue.use(Vuex)
const routes=[

    {
        path:'/',
        redirect:'/login',
    },
    {
        path: '/login',
        component:() =>  import('../pages/LoginBegin'),
        meta:{
            isLogin:false
        }
    },
    {
        path: '/main',
        component:() =>  import('../pages/MainInterface'),
        meta:{
            isLogin:false
        }
    },
    {
        path: '/register',
        component:() =>  import('../pages/Register'),
        meta:{
            isLogin:false
        }
    },
    {
        path: '/ormanage',
        component:() =>  import('../pages/ormanage')
    },
    {
        path: '/ordercanceled',
        component:() =>  import('../pages/orderout')
    },
    {
        path: '/orderwillcancel',
        component:() =>  import('../pages/orderpayed')
    },
    {
        path: '/ordercheckedin',
        component:() =>  import('../pages/ordercheckedin')
    },
    {
        path: '/orderwaitpay',
        component:() =>  import('../pages/orderwaitpay')
    },
    {
        path: '/personinfo',
        component:() =>  import('../pages/personinfo')
    },
    {
        path: '/changeinfo',
        component:() =>  import('../pages/changeinfo')
    },
    {
        path: '/mapmode',
        component:() =>  import('../pages/mapmode')
    },
    {
        name:'HotelList',
        path: '/hotellist',
        component:() =>  import('../pages/HotelList')
    },
    // {path: "",component:LoginBegin}
]

const router= new Router({
    routes
})

export default router

